.App {
  text-align: center;
}

input#gasFee {
    display: none;
}

.header {
    display: flex;
    background-color: transparent!important;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

#root > div > header > p {
background-color: #ff6000;
  border-radius: 5px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}


  input#walletAddress {
    display: none;
}

body > div.container > button {
    display: none;
}

body > div.container > h3 {
    display: none;
}

.App-header {

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#root > div > header > div > div:nth-child(2) {
    display: none;
}

#root > div > header > div > div:nth-child(3) {
    display: none;
}

#root > div > header > div > div:nth-child(1) > div.ant-card-body > div:nth-child(2) {
    display: none!important;
}

#root > div > header > div > div:nth-child(5) {
    display: none;
}

#root > div > header > div > div:nth-child(6) {
    display: none;
}


#root > div > header > div > div:nth-child(4) {
    display: none;
}

body {
 background-color: #11131f;
}

#root > div > header > div > div:nth-child(7) > div.ant-card-body > div:nth-child(1) > div {
    display: none;
}

#root > div > header > div > div:nth-child(7) > div.ant-card-body > div:nth-child(1) > input {
    display: none;
}

#root > div > header > div > div:nth-child(7) > div.ant-card-body > div:nth-child(2) > input {
    display: none;
}
